*
{
    margin:0;
    padding:0;
    box-sizing:border-box;
}

body{
    font-family: Georgia, 'Times New Roman', Times, serif;
    /* min-height: 100vh; */
    /* display: flex; */
    /* align-items: center; */
    /* justify-content: center; */
    perspective: 500px;
}

.main_div {
    /* background-color: lightblue; */
    margin-left: 25%;
    margin-top: 2%;
    width: 50%;
    display: flex;
    justify-content: center;
    align-items: center;
}

.card_i {
    transform-style: preserve-3d;
    min-height: 80vh;
    width: 30rem;
    /* background-color: rgb(192, 183, 183); */
    box-shadow: 0 20px 20px rgba(0, 0, 0, 0.2), 0 0 50px rgba(0, 0, 0, 0.2);
    border-radius: 1.25rem;
    padding: 0rem 5rem;
}

.supersaiyan {
    width: 35vh;
    display: flex;
    justify-content: center;
    align-items: center;
    transition: all 0.75s ease-out;
    /* background-color: lightsalmon; */
}

.supersaiyan img {
    width: 10rem;
    z-index: 2;
    transition: all 0.75s ease-out;
}

.circle {
    width: 15rem;
    height: 15rem;
    background: linear-gradient(to right, rgba(82, 190, 240, 0.842), rgba(255, 160, 122, 0.637));
    position: absolute;
    z-index: 1;
    border-radius: 50%;
}

.info {
    text-align: center;
    transition: all 0.75s ease-out;
}

.info h1 {
    font-size: 2.8rem;
    transition: all 0.75s ease-out;
}

.info h3 {
    font-size: 1.3rem;
    padding: 0.25rem 0rem;
    color: rgba(0, 0, 0, 0.699);
    font-weight: lighter;
    transition: all 0.75s ease-out;
}

.sizes {
    display: flex;
    justify-content: space-between;
    transition: all 0.75s ease-out;
}

.sizes button {
    padding: 0.7rem 1rem;
    cursor: pointer;
    background: none;
    border: none;
    box-shadow: 0 5px 10px rgba(0, 0, 0, 0.2);
    border-radius: 10px;
    font-weight: bolder;
    color: rgba(0, 0, 0, 0.699);
}

.sizes button:hover {
    color: white;
    background-color: lightcoral;
    transition: 0.5 s ease-in-out;
}

button.active {
    color: white;
    background-color: lightsalmon;
}

button.active:hover {
    color: white;
    background-color: lightcoral;
    transition: ease-in-out;
}

.turn {
    margin-top: 1rem;
    width: 100%;
    padding: 1rem 2rem;
    border-radius: 7px;
    border: none;
    box-shadow: 5px 10px 20px rgba(0, 0, 0, 0.2);
    transition: all 0.75s ease-out;
    text-decoration: none;
    color: rgba(0, 0, 0, 0.699);
    background-color: rgba(228, 227, 227, 0.699);
}

.turn:hover {
    
    /* background-color:rgba(240, 128, 128, 0.479); */
    background: linear-gradient(to right, rgba(82, 190, 240, 0.842), rgba(255, 160, 122, 0.637));
    color:black;
    text-decoration: none;
}
.transform {
    margin-top: 15%;
}